<template lang="html">
  <div class="car-dingwei">
    <div class="car-dw">
        <div class="header clearfix">
            <span class="fl">车辆定位</span>
            <router-link to="/WDCD" tag="span" class="el-icon-close fr"></router-link>
        </div>
        <div class="body">
          <div class="clearfix body_1">
            <img src="../../../assets/u151_03.png" class="fl"/>
            <div class="fl">
              <p class="name">衣龙魁</p>
              <p>
                车牌号码：XXXX
              </p>
              <p>
                联系方式：XXXXXXXXXXXX
              </p>
            </div>
          </div>
          <div class="body_2">
              <div class="b_tex">
                <p>
                  车辆类型：XXXXXXXXXXXX
                </p>
                <p>
                  车辆长度：XXXXXXXXXXXX
                </p>
                <p>
                  身份证号：XXXXXXXXXXXX
                </p>
              </div>
              <div class="ditu" id="allmap"></div>
              <div class="dw-footer">
                  <router-link to="/WDCD" tag="button" class="btn">确认</router-link>
              </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
    mounted(){
      var map = new BMap.Map("allmap");
      	map.centerAndZoom(new BMap.Point(116.4035,39.915),8);
      	setTimeout(function(){
      		map.setZoom(14);
      	}, 2000);  //2秒后放大到14级
      	map.enableScrollWheelZoom(true);
    }
}
</script>

<style lang="less" scoped>
    *{
      margin: 0;
      padding:0;
    }
    .clearfix:after{
      content: "";
      display: block;
      clear: both;
    }
    .fl{
      float: left;
    }
    .fr{
      float: right;
    }
    .car-dingwei{
      width: 100%;
      height: 1000px;
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.3);
      .car-dw{
        background: white;
        box-sizing: border-box;
        width:700px;
        height:720px;
        position: absolute;
        top:0;
        left:0;
        right:0;
        bottom: 0;
        margin:auto;
        .header{
          padding:17px 17px;
          border-bottom: 1px solid #E9E9E9;
          font-size:16px;
          font-weight:700;
          .el-icon-close{
            cursor: pointer;
          }
        }
        .body{
          padding:0 20px;
          .body_1{
            padding:20px 0;
            border-bottom: 1px solid #E6E8F4;
            img{
              margin-right: 20px;
            }
            .name{
              font-size: 18px;
              font-weight: bold;
              margin-bottom: 18px;
            }
          }
          .body_2{
              .b_tex{
                padding: 30px 0 10px 0;
                p{
                  margin-bottom: 15px;
                }
              }
              .ditu{
                width: 100%;
                height: 318px;
                border: 1px solid white;
              }
          }
        }
        .dw-footer{
          text-align: right;
          .btn{
            width: 90px;
            height: 30px;
            background: #0C9B0B;
            border: 1px solid #0C9B0B;
            margin-top:30px;
            color: white;
            cursor: pointer;
          }
        }
      }
    }
</style>



